import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes} from '@angular/router';
import { HttpClientModule } from '@angular/common/http';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

import { ChangeUserPasswordComponent } from './change-user-password/change-user-password.component';
import { ChangeUserPasswordFormComponent } from './change-user-password/change-user-password-form/change-user-password-form.component';

import { UserService } from '../services/user.service';
import { ShoppingCartService } from '../services/shopping.cart.service';
import { IsAuthGuard } from '../services/check-auth.service';

import { HeaderModule } from '../header/header.module';
import { FooterModule } from '../footer/footer.module';

const userProfileRoutes: Routes = [
  { path: 'change_user_password', component: ChangeUserPasswordComponent, canActivate: [IsAuthGuard]}
];

@NgModule({
  declarations: [
    ChangeUserPasswordComponent,
    ChangeUserPasswordFormComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(userProfileRoutes),
    HeaderModule,
    FooterModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule
  ],
  providers: [
    UserService,
    ShoppingCartService,
    IsAuthGuard
  ],
  exports: [RouterModule]
})
export class UserProfileModule { }
