import { Component, OnInit } from '@angular/core';

import { UserService } from '../../services/user.service';
import { ShoppingCartService } from '../../services/shopping.cart.service';

@Component({
  selector: 'app-change-user-password',
  templateUrl: './change-user-password.component.html',
  styleUrls: ['./change-user-password.component.css']
})
export class ChangeUserPasswordComponent implements OnInit {

  errorMsg: string = "";
  successMsg: string = "";
  total_qty: number = 0;

  constructor(
    private userService: UserService,
    private shoppingCart: ShoppingCartService,
    ) {}

  ngOnInit() {
    this.getTotalQuantity();
  }

  getTotalQuantity() {
    this.shoppingCart.getTotalQuantity()
      .subscribe(
        (data: any) => {
          this.total_qty = data.totalQuantity;
        },
        err => console.log('error getting item quantity'),
        () => console.log('complete getting item quantity')
      );
  }

  changePw(event: any) {
    this.errorMsg = "";
    this.successMsg = "";
    this.userService.changePassword(event.currentPw, event.newPw)
    .subscribe(data => {
      this.successMsg = "Password changed successfully"
      this.errorMsg = "";
    },
    err => {
      this.errorMsg = err.error.msg;
      console.log(err);
    },
    () => console.log('update password complete')
    );
  }
}
