import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes} from '@angular/router';
import { HttpClientModule } from '@angular/common/http';

import { PurchaseHistoryComponent } from './purchase-history/purchase-history.component';
import { PurchaseHistoryDetailsComponent } from './purchase-history-details/purchase-history-details.component';

import { UserService } from '../services/user.service';
import { ShoppingCartService } from '../services/shopping.cart.service';
import { IsAuthGuard } from '../services/check-auth.service';

import { HeaderModule } from '../header/header.module';
import { FooterModule } from '../footer/footer.module';
import { InvoiceModule } from '../invoice/invoice.module';

const purchaseHistoryRoutes: Routes = [
  { path: 'purchase_history/order_details/:id', component: PurchaseHistoryDetailsComponent, canActivate: [IsAuthGuard]},
  { path: 'purchase_history', component: PurchaseHistoryComponent, canActivate: [IsAuthGuard]}
];

@NgModule({
  declarations: [
    PurchaseHistoryComponent,
    PurchaseHistoryDetailsComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(purchaseHistoryRoutes),
    HeaderModule,
    FooterModule,
    InvoiceModule,
    HttpClientModule
  ],
  providers: [
    UserService,
    ShoppingCartService,
    IsAuthGuard
  ],
  exports: [RouterModule]
})

export class PurchaseHistoryModule { }
