import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes} from '@angular/router';
import { HttpClientModule } from '@angular/common/http';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

import { UserService } from '../services/user.service';
import { ShoppingCartService } from '../services/shopping.cart.service';
import { IsNotAuthGuard } from '../services/check-not-auth.service';

import { HeaderModule } from '../header/header.module';
import { FooterModule } from '../footer/footer.module';

import { ForgotUserPasswordComponent } from './forgot-user-password.component';
import { ForgotUserPasswordFormComponent } from './forgot-user-password-form/forgot-user-password-form.component';

const forgotUserPasswordRoutes: Routes = [
  { path: 'forgot', component: ForgotUserPasswordComponent, canActivate: [IsNotAuthGuard]}
];

@NgModule({
  declarations: [
    ForgotUserPasswordComponent,
    ForgotUserPasswordFormComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(forgotUserPasswordRoutes),
    HeaderModule,
    FooterModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule
  ],
  providers: [
    UserService,
    ShoppingCartService,
    IsNotAuthGuard
],
exports: [RouterModule]
})
export class ForgotUserPasswordModule { }
