import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes} from '@angular/router';
import { HttpClientModule } from '@angular/common/http';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

import { UserService } from '../services/user.service';
import { ShoppingCartService } from '../services/shopping.cart.service';
import { IsNotAuthGuard } from '../services/check-not-auth.service';

import { PasswordResetComponent } from './password-reset/password-reset.component';
import { PasswordResetFormComponent } from './password-reset/password-reset-form/password-reset-form.component';

import { HeaderModule } from '../header/header.module';
import { FooterModule } from '../footer/footer.module';

const passwordResetRoutes: Routes = [
  { path: 'reset/:token', component: PasswordResetComponent, canActivate: [IsNotAuthGuard]}
];

@NgModule({
  declarations: [
    PasswordResetComponent,
    PasswordResetComponent,
    PasswordResetFormComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(passwordResetRoutes),
    HeaderModule,
    FooterModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule
  ],
  providers: [
    UserService,
    ShoppingCartService,
    IsNotAuthGuard
  ],
  exports: [RouterModule]
})
export class PasswordResetModule { }
