import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-forgot-user-password-form',
  templateUrl: './forgot-user-password-form.component.html',
  styleUrls: ['./forgot-user-password-form.component.css']
})
export class ForgotUserPasswordFormComponent implements OnInit {

  @Input()
  errorMsg: string;

  @Input()
  successMsg: string;

  @Output()
  forgotPasswordEmitter = new EventEmitter<any>();

  emailMsg: string = '';

  forgotPwForm: FormGroup;

  constructor(private fb: FormBuilder) { }

  ngOnInit() {
    this.createForm();
  }

  clearMsg() {
    this.emailMsg = "";
    this.errorMsg = "";
    this.successMsg = "";
  }

  createForm() {
    this.forgotPwForm = this.fb.group({
      email: ['', [Validators.required, Validators.pattern("^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$")]]
    });
  }

  resetPassword(_forgotPwForm: any) {
    this.clearMsg();
    // console.log(_forgotPwForm);
    if (_forgotPwForm.controls.email.errors) {
      if (_forgotPwForm.controls.email.errors.required) {
        this.emailMsg = "Email is required";
      }
      else {
        this.emailMsg = "Invalid email address";
      }
    }
    else {
      let email = _forgotPwForm.controls.email.value;
      this.forgotPasswordEmitter.emit({email});
      _forgotPwForm.reset();
    }

  }

}
